import { API } from './api';
import { AxiosRequestConfig } from 'axios';
// import { IRootOrderResponse } from 'utils/interfaces';
/**
 *  Api service used to manage & handle orders
 */

export class ProofDeliveryService {
  async getProofDelivery() {
    //request config
    const config: AxiosRequestConfig = {
      method: 'GET',
      url: `/order-delivery-details`,
    };

    const response = await API.request(config);
    return response.data;
  }
}

const proofDeliveryService = new ProofDeliveryService();

export default proofDeliveryService;
