import React, { useMemo, useReducer, useState } from 'react';
import _ from 'lodash';
import { ReactComponent as Fold } from 'assets/images/icons/fold.svg';
import { ReactComponent as Hung } from 'assets/images/icons/hung.svg';
import { ReactComponent as NoResultIcon } from 'assets/images/icons/no-result.svg';
import { ReactComponent as Shirt } from 'assets/images/icons/shirt.svg';

import { Accordion, BagTypeIcon, BgImageWithChildAndIcon, CarouselSlider, ContentPlaceHolder, FloatingSearch, ItemDetailsDialog } from 'components';
import { DeliveryItem, InfoCodeParser, OrderDelivery, Package, TApprovalStatus, getCodeFromError, getPluralSuffix } from 'utils';
import { ProofOfDeliveryReducer, TInitialStates } from '../../proofOfDeliveryReducer';
import { ProofDeliveryService } from 'services';
import ErrorMessage from 'components/errorMessage';
import styled from 'styled-components';

const initialState: TInitialStates = {
  order: [],
  loading: true,
  error: {
    isError: false,
  },
};

const ProofOfDelivery: React.FC = () => {
  const [searchValue, setSearchValue] = useState('');
  const [
    {
      order,
      loading,
      error: { isError, message, code },
    },
    dispatch,
  ] = useReducer(ProofOfDeliveryReducer, initialState);

  const apiCalling = async () => {
    try {
      dispatch({ type: 'GET_PROOF_DELIVERY_START' });
      const { orderDeliveryDetails } = await ProofDeliveryService.getProofDelivery();
      dispatch({ type: 'GET_PROOF_DELIVERY_SUCCESS', order: orderDeliveryDetails });
    } catch (error) {
      if (error.isAxiosError) {
        let message = 'unknown.error'; // default message
        message = error.response ? InfoCodeParser(error.response.data.infoCodes) : error.message;
        const code = getCodeFromError(error);
        dispatch({ type: 'GET_PROOF_DELIVERY_FAILED', message, code });
      }
    } finally {
      dispatch({ type: 'GET_PROOF_DELIVERY_COMPLETED' });
    }
  };

  React.useEffect(() => {
    apiCalling();
  }, []);

  const WF_STATUSES: TApprovalStatus[] = ['PENDING_WF_TO_CP', 'PROCEED_WF'];

  const PackageItem = ({ item }: { item: DeliveryItem }) => {
    const [isDialogVisible, setDialogVisible] = useState(false);

    const handleImageClick = () => {
      setDialogVisible(!isDialogVisible);
    };
    const showBrandName = item?.brand !== 'none' ? item.brand?.replace(/_/g, ' ').toLowerCase() : 'no brand';
    const showItemTypeName = item?.itemType ? item.itemType?.replace(/_/g, ' ').toLowerCase() : 'no type';
    return (
      <>
        <BgImageWithChildAndIcon
          key={item.id}
          url={item.frontImage}
          text={showBrandName}
          handleImageClick={handleImageClick}
          icon={<BagTypeIcon iconSize="1rem" bagType={item?.approvalStatus && WF_STATUSES.includes(item.approvalStatus) ? 'WF' : item?.bagType} />}
          isGalleryIconShow={true}
          reportDetailsList={item.reportDetailsList}
        />
        <ItemDetailsDialog
          data={[
            { url: item.frontImage, reason: 'Original', bagType: item.bagType, approvalStatus: item.approvalStatus },
            ...item.reportDetailsList.map((_item) => ({
              ..._item,
              approvalStatus: item.approvalStatus,
              bagType: item.bagType,
              reason: _item.reason.toLocaleLowerCase(),
            })),
          ]}
          show={isDialogVisible}
          itemCode={item.id}
          itemColors={item.itemColorsList}
          brand={showBrandName}
          itemType={showItemTypeName}
          onHide={() => setDialogVisible(false)}
          selectedSlideIndex={0}
          // singleImageUrl={item?.reportDetailsList?.length > 0 ? '' : item.frontImage}
          activities={item.activities}
        />
      </>
    );
  };
  const addS = (num: number) => (num > 1 ? 's' : '');
  const renderPackageImages = (packages: Package[], packageType: string) => {
    let count = 0;
    return (
      <>
        {packages?.map((packageItems: Package, index: number) => (
          <React.Fragment key={packageItems.id}>
            <div className="d-flex justify-content-between">
              <SubHeadingText>{`${packageType} Package ${++count}`}</SubHeadingText>
              <SubHeadingText>{`${packageItems.numberOfItems} Item${addS(packageItems.numberOfItems)}`}</SubHeadingText>
            </div>
            <BgImageParent>
              {filterItems(packageItems.items).map((item) => (
                <PackageItem key={item.id} item={item} />
              ))}
            </BgImageParent>
          </React.Fragment>
        ))}
      </>
    );
  };

  const getImageUrls = (images: string | string[] | undefined, text: string): { url: string; reason: string }[] => {
    if (typeof images === 'string') {
      return [{ url: images, reason: text }];
    } else if (Array.isArray(images)) {
      return images.map((url, index) => ({ url, reason: text + ` ${index + 1}` }));
    } else {
      return [];
    }
  };

  const RenderDeliveryImages: React.FC<any> = ({ dropoffImageHooks, dropoffImageDoorNumber, dropoffImageHangersList, isShoeCare, shoesCount }) => {
    const [selectedSlideIndex, setSelectedSlideIndex] = useState<number>(0);
    const [showImgDialog, setShowImgDialog] = useState<boolean>(false);
    const handleImageClick = (val: number) => {
      setSelectedSlideIndex(val);
      setShowImgDialog(!showImgDialog);
    };

    const everythingAtDoor = Array.isArray(dropoffImageHangersList) && dropoffImageHangersList.length > 1 ? dropoffImageHangersList[1] : [];
    const hangImage = Array.isArray(dropoffImageHangersList) && dropoffImageHangersList.length > 0 ? dropoffImageHangersList[0] : [];
    const images = [
      ...getImageUrls(everythingAtDoor, 'Everything at Door'),
      ...getImageUrls(dropoffImageDoorNumber, 'Door Image'),
      ...getImageUrls(hangImage, `${isShoeCare ? (shoesCount <= 1 ? 'Shoe Bag' : 'Shoe Package') : 'Hangers'}`),
    ];

    if (images.length === 0) {
      return null;
    }

    return (
      <BgImageParentDelivery>
        <CarouselSlider onPress={handleImageClick} data={images} />
        <ItemDetailsDialog
          headerTitle="Delivery Images"
          data={images}
          isShowImagesOnly={true}
          show={showImgDialog}
          onHide={() => setShowImgDialog(false)}
          selectedSlideIndex={selectedSlideIndex}
        />
      </BgImageParentDelivery>
    );
  };

  const renderFilterDeliveryPackages = (packages: Package[]) => {
    const flattenedPackagesArray = flattenPackages(packages);

    return <>{renderPackageContent(flattenedPackagesArray)}</>;
  };

  const flattenPackages = (packages: Package[]) => {
    return packages.flatMap((packageItems: Package) => {
      const filteredItems = filterItems(packageItems.items);
      return filteredItems.map((item) => ({
        item: item,
        url: item?.frontImage,
        reason: item?.brand,
      }));
    });
  };

  const renderPackageContent = (flattenedPackagesArray: any[]) => {
    return (
      // <div className="h-100">
      <SearchResultContainer>
        <SearchResultParent>
          {flattenedPackagesArray.length === 0 ? (
            <NoResult>
              <Shirt />
              <br />
              <NoResultIcon />
            </NoResult>
          ) : (
            <RenderPackageDetails flattenedPackagesArray={flattenedPackagesArray} />
          )}
        </SearchResultParent>
      </SearchResultContainer>
      // </div>
    );
  };

  const RenderPackageDetails: React.FC<any> = ({ flattenedPackagesArray }) => {
    const [isItemDialogVisible, setItemDialogVisible] = useState(false);
    const [itemItemIndex, setItemItemIndex] = useState(0);

    const showBrandName =
      flattenedPackagesArray[itemItemIndex].item?.brand !== 'none'
        ? flattenedPackagesArray[itemItemIndex].item.brand?.replace(/_/g, ' ').toLowerCase()
        : 'no brand';

    const showItemTypeName = flattenedPackagesArray[itemItemIndex]?.item.itemType
      ? flattenedPackagesArray[itemItemIndex].item.itemType?.replace(/_/g, ' ').toLowerCase()
      : 'no type';

    const handleItemDialogToggle = (itemItemIndex: number) => {
      setItemDialogVisible(!isItemDialogVisible);
      setItemItemIndex(itemItemIndex);
    };

    return (
      <>
        <CarouselSlider data={flattenedPackagesArray} isDeliveryItems={true} onPress={handleItemDialogToggle} />
        <ItemDetailsDialog
          data={[
            {
              url: flattenedPackagesArray[itemItemIndex].item.frontImage,
              reason: 'Original',
              bagType: flattenedPackagesArray[itemItemIndex].item.bagType,
              approvalStatus: flattenedPackagesArray[itemItemIndex].item.approvalStatus,
            },
            ...flattenedPackagesArray[itemItemIndex].item.reportDetailsList.map((_item: any) => ({
              ..._item,
              approvalStatus: flattenedPackagesArray[itemItemIndex].item.approvalStatus,
              bagType: flattenedPackagesArray[itemItemIndex].item.bagType,
              reason: flattenedPackagesArray[itemItemIndex].reason.toLocaleLowerCase(),
            })),
          ]}
          show={isItemDialogVisible}
          itemCode={flattenedPackagesArray[itemItemIndex].item.id}
          itemColors={flattenedPackagesArray[itemItemIndex].item.itemColorsList}
          brand={showBrandName}
          itemType={showItemTypeName}
          onHide={() => setItemDialogVisible(false)}
          selectedSlideIndex={0}
          activities={flattenedPackagesArray[itemItemIndex].item.activities}
        />
      </>
    );
  };

  const renderSinglePackage = (packageDetails: any) => {
    const { item } = packageDetails;

    return (
      <>
        <BgImageWithChildAndIcon
          key={item.id}
          url={item.frontImage}
          text={item.brand}
          handleImageClick={() => {}}
          icon={<BagTypeIcon iconSize="1rem" bagType={item?.approvalStatus && WF_STATUSES.includes(item.approvalStatus) ? 'WF' : item?.bagType} />}
          isGalleryIconShow={true}
          reportDetailsList={item.reportDetailsList}
        />
      </>
    );
  };

  const renderWhenWhereDetails = ({ dropoffAddress, isDeliverAtDoor, dropoffDurationText }: OrderDelivery) => {
    const dropoffText = dropoffDurationText.replace(/AM|PM/gi, (matched) => matched.toLowerCase());
    const { addressType, number, building, street, villa } = dropoffAddress;
    const Item = ({ name, value }: { name: string; value: string }) => (
      <div className="d-flex">
        <WhenWhereKey>{name}:</WhenWhereKey>
        <WhenWhereValue>{value} </WhenWhereValue>
      </div>
    );
    return (
      <div className="mt-0 mb-4">
        <Item name="Where" value={addressType !== 'Villa' ? building + ', ' + number : street + ', ' + villa} />
        <Item name="When" value={dropoffText} />
        <Item name="How" value={`${isDeliverAtDoor ? 'At door' : 'In person'}`} />
      </div>
    );
  };

  const filterItems = (items: DeliveryItem[]) =>
    items.filter(
      (item) =>
        item.brand.toLowerCase().includes(searchValue.toLowerCase()) ||
        item.type.toLowerCase().includes(searchValue.toLowerCase()) ||
        item.code.toLowerCase().includes(searchValue.toLowerCase()) ||
        item.id.toLowerCase().includes(searchValue.toLowerCase()) ||
        item.itemColorsList.some((color) => color.name.toLowerCase().includes(searchValue.toLowerCase()))
    );

  const renderOrderDeliveryDetails = useMemo(() => {
    if (loading) return <ContentPlaceHolder amount={3} />;
    if (isError && !loading && !order?.length) return <ErrorMessage code={code} message={message} />;
    return (
      order &&
      order?.map((data: OrderDelivery, index: number) => {
        const isShoeCare = data.serviceLine === 'shoes';
        const itemCount = index + 1;
        const filteredPackages = data.packages?.filter((pkg: Package) => pkg.items.some((item: DeliveryItem) => filterItems([item]).length > 0));
        const hungPackages = filteredPackages?.filter((pkg: Package) => pkg.items.some((item: DeliveryItem) => item.isHung));
        const foldedPackages = filteredPackages?.filter((pkg: Package) => pkg.items.some((item: DeliveryItem) => !item.isHung));
        return (
          <>
            <SubContainer key={data.id}>
              {searchValue === '' ? (
                <>
                  <ProofOfDeliveryHeadingText>Proof of Delivery {itemCount > 1 ? itemCount : ''} </ProofOfDeliveryHeadingText>
                  <WhereWhenContainer>{renderWhenWhereDetails(data)}</WhereWhenContainer>
                  <RenderDeliveryImages
                    dropoffImageHooks={data.dropoffImageHooks}
                    dropoffImageDoorNumber={data.dropoffImageDoorNumber}
                    dropoffImageHangersList={data.dropoffImageHangersList}
                    isShoeCare={isShoeCare}
                    shoesCount={foldedPackages.length}
                  />
                  {foldedPackages.length > 0 && (
                    <Accordion
                      title={`Folded Packages`}
                      children={renderPackageImages(foldedPackages, 'Folded')}
                      // title={`${isShoeCare ? 'Shoes' : 'Folded'} Packages`}
                      // children={renderPackageImages(foldedPackages, `${isShoeCare ? 'Shoes' : 'Folded'}`)}
                      startIcon={<Fold />}
                      count={foldedPackages.length}
                    />
                  )}
                  {hungPackages.length > 0 && (
                    <Accordion
                      title={`Hung Packages`}
                      children={renderPackageImages(hungPackages, 'Hung')}
                      startIcon={<Hung />}
                      count={hungPackages.length}
                    />
                  )}
                </>
              ) : (
                <>{renderFilterDeliveryPackages(filteredPackages)}</>
              )}
            </SubContainer>
            {data?.packages?.length > 0 && (
              <FloatingSearch
                placeholder="Search for items type, colors, brands"
                value={searchValue}
                setSearchValue={(value) => setSearchValue(value)}
              />
            )}
          </>
        );
      })
    );
  }, [order, loading, isError, searchValue]);

  return (
    <Container color="#291482">
      {renderOrderDeliveryDetails}
      <span className="hidden"></span>
    </Container>
  );
};
const Container = styled.div`
  flex: 1;
  color: #291482;
  max-width: 375px;
  height: 100vh;
  line-height: 24px;
  letter-spacing: 0.4px;
  font-family: 'Inter Light';
  margin: 0px;
`;
// padding: 16px; this cause have some issues

const SubContainer = styled.div`
  max-width: 375px;
  height: calc(100vh - 0px);
  overflow: auto;
  padding: 0 24px;
`;

const BgImageParent = styled.div`
  padding: 0px 0px 0px 0px;
  justify-content: space-between;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
`;

const SearchResultContainer = styled.div`
  align-items: center;
  height: 100%;
  width: 100%;
  flex: 1;
  display: flex;
  justify-content: center;
`;
const SearchResultParent = styled.div`
  width: 100%;
`;

const WhenWhereKey = styled.span`
  color: #291482;
  font-family: 'Inter Regular';
  font-size: 12px;
  line-height: 18px;
  margin-bottom: 8px;
  letter-spacing: 0.1px;
  font-weight: 600;
`;

const WhenWhereValue = styled.span`
  color: #291482;
  font-family: 'Inter Regular';
  font-size: 12px;
  line-height: 18px;
  margin-bottom: 8px;
  letter-spacing: 0.1px;
`;

const ProofOfDeliveryHeadingText = styled.p`
  font-family: 'Inter H1 Bold';
  padding-top: 16px;
  font-size: 20px;
  margin: 0px;
  padding-bottom: 24px;
`;

const NoResult = styled.div`
  margin-top: 5vh;
  text-align: center;
  line-height: 73px;
`;

const WhereWhenContainer = styled.div`
  display: flex;
`;

const SubHeadingText = styled.h3`
  color: #291482;
  text-align: left;
  position: relative;
  padding-top: 16px;
  padding-bottom: 16px;
  margin: 0px;
  font-family: 'Inter Medium';
  font-size: 14px;
  line-height: 20px;
`;

const BgImageParentDelivery = styled.div`
  margin: 0px 0px 35px 0px;
`;

export default ProofOfDelivery;
