import React from 'react';
import { withRouter, Route, RouteComponentProps, Switch, Redirect } from 'react-router-dom';
import { ProofOfDelivery } from './pages';
import './proofOfDeliveryRoute.scss';
interface Props extends RouteComponentProps {}

const proofOfDeliveryRoute: React.FC<Props> = ({ match: { path } }) => {
  return (
    <Switch>
      <Route path={`${path}`} children={<ProofOfDelivery />} />
      <Redirect to={path} />
    </Switch>
  );
};

export default withRouter(proofOfDeliveryRoute);
