import { API } from './api';
import { AxiosRequestConfig } from 'axios';
// import { IRootOrderResponse } from 'utils/interfaces';
/**
 *  Api service used to manage & handle orders
 */

export class ProofCollectionService {
  async getProofCollection() {
    //request config
    const config: AxiosRequestConfig = {
      method: 'GET',
      url: `/order-pickup-details`,
    };

    const response = await API.request(config);
    return response.data;
  }
}

const proofCollectionService = new ProofCollectionService();

export default proofCollectionService;
