import { Reducer } from 'react';
import { OrderDelivery } from 'utils/interfaces';

export type OrderReducerAction =
  | { type: 'GET_PROOF_DELIVERY_START' }
  | { type: 'GET_PROOF_DELIVERY_COMPLETED' }
  | { type: 'GET_PROOF_DELIVERY_SUCCESS'; order: OrderDelivery[] }
  | { type: 'GET_PROOF_DELIVERY_FAILED'; message?: string; code?: string };

export interface TInitialStates {
  loading: boolean;
  error: {
    isError: boolean;
    message?: string;
    code?: string;
  };
  order?: OrderDelivery[];
}

export type TOrderReducer = Reducer<TInitialStates, OrderReducerAction>;

export const ProofOfDeliveryReducer: Reducer<TInitialStates, OrderReducerAction> = (state, action) => {
  switch (action.type) {
    case 'GET_PROOF_DELIVERY_START': {
      return {
        ...state,
        loading: true,
      };
    }
    case 'GET_PROOF_DELIVERY_SUCCESS': {
      return {
        ...state,
        loading: false,
        order: action.order,
      };
    }
    case 'GET_PROOF_DELIVERY_COMPLETED': {
      return {
        ...state,
        loading: false,
      };
    }
    case 'GET_PROOF_DELIVERY_FAILED': {
      return {
        ...state,
        loading: false,
        error: {
          isError: true,
          message: action.message,
          code: action.code,
        },
      };
    }

    default:
      return state;
  }
};
