import { API } from './api';
import { AxiosRequestConfig } from 'axios';
import { IRootOrderResponse, TApprovalStatus, IRootOrderObject, TUserPreferences } from 'utils/interfaces';
/**
 *  Api service used to manage & handle orders
 */
export class OrderService {
  /**
   * Get Order details
   *
   * @async
   * @function getOrder
   *
   * @return {object} Order Details
   */
  async getItemsByOrder() {
    //request config
    const config: AxiosRequestConfig = {
      method: 'GET',
      url: '/v2/orders/items',
    };

    const response = await API.request<IRootOrderResponse>(config);
    return response.data;
  }

  async updateApprovalStatus(itemCode: string, approvalStatus: TApprovalStatus) {
    //request config
    const config: AxiosRequestConfig = {
      method: 'PUT',
      url: `/orders/items/${itemCode}/approval-status`,
      data: {
        itemCode,
        approvalStatus,
      },
    };

    const response = await API.request<IRootOrderResponse>(config);
    return response;
  }

  async updatePreferences(data: TUserPreferences) {
    //request config
    const config: AxiosRequestConfig = {
      method: 'PUT',
      url: '/customers/future/preferences',
      data: data,
    };

    const response = await API.request<IRootOrderResponse>(config);
    return response;
  }

  /**Get Complete Billing Info */
  async getBillingInfo() {
    //request config
    const config: AxiosRequestConfig = {
      method: 'GET',
      url: `v2/orders/billing`,
    };
    let response = await API.request<IRootOrderObject>(config);

    return response.data;
  }
}

const orderService = new OrderService();

export default orderService;
